.auth-page__submit {
	width: 100%;
	height: 50px;
	margin-top: 216px;
	margin-bottom: 15px;
	font-size: 18px;
	line-height: 1;
	border: none;
	border-radius: 2px;
	color: #000;
	background-color: #fff;
	transition: visibility 0.2s, opacity 0.1s linear;
}

.home__submit:hover {
	cursor: pointer;
	opacity: .8;
}